import React from 'react'
import { useState } from 'react'
import '@chatscope/chat-ui-kit-styles/dist/default/styles.min.css';
import { MainContainer, ChatContainer, MessageList, Message, MessageInput, TypingIndicator } from '@chatscope/chat-ui-kit-react';
import { Grid } from '@mui/material';
import { useEffect } from 'react';
import AudioScreen from './AudioScreen';
const API_KEY = "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";
// "Explain things like you would to a 10 year old learning how to code."
const systemMessage = { //  Explain things like you're talking to a software professional with 5 years of experience.
  "role": "system", "content": "Explain things like you're talking to a software professional with 2 years of experience."
}
const ChatScreen = () => {
  const [isTyping, setIsTyping] = useState(true);
  const [messages, setMessages] = useState([]);
  
  useEffect(() => {
    setTimeout(() => {
      setMessages([
      {
        message: "Hello, I'm AiBot! Designed By Shahzaib !",
        sentTime: "just now",
        sender: "ChatGPT"
      },
      {
        message: "You can ask me Anything",
        sentTime: "just now",
        sender: "ChatGPT"
      }])
      setIsTyping(false)
    }, 3000);
  }, [])

      const handleSend = async (message) => {
        const newMessage = {
          message,
          direction: 'outgoing',
          sender: "user"
        };
    
        const newMessages = [...messages, newMessage];
        
        setMessages(newMessages);
    
        // Initial system message to determine ChatGPT functionality
        // How it responds, how it talks, etc.
        setIsTyping(true);
        await processMessageToChatGPT(newMessages);
      };
    
      async function processMessageToChatGPT(chatMessages) { // messages is an array of messages
        // Format messages for chatGPT API
        // API is expecting objects in format of { role: "user" or "assistant", "content": "message here"}
        // So we need to reformat
    
        let apiMessages = chatMessages.map((messageObject) => {
          let role = "";
          if (messageObject.sender === "ChatGPT") {
            role = "assistant";
          } else {
            role = "user";
          }
          return { role: role, content: messageObject.message}
        });
    
    
        // Get the request body set up with the model we plan to use
        // and the messages which we formatted above. We add a system message in the front to'
        // determine how we want chatGPT to act. 
        const apiRequestBody = {
          "model": "gpt-3.5-turbo",
          "messages": [
            systemMessage,  // The system message DEFINES the logic of our chatGPT
            ...apiMessages // The messages from our chat with ChatGPT
          ]
        }
    
        await fetch("https://api.openai.com/v1/chat/completions", 
        {
          method: "POST",
          headers: {
            "Authorization": "Bearer " + API_KEY,
            "Content-Type": "application/json"
          },
          body: JSON.stringify(apiRequestBody)
        }).then((data) => {
          return data.json();
        }).then((data) => {
          console.log(data);
          setMessages([...chatMessages, {
            message: data.choices[0].message.content,
            sender: "ChatGPT"
          }]);
          setIsTyping(false);
        });
      }
    
      return (
        <Grid container>

          <Grid item p={{sm:2, xs:3}} pt={{sm:8}} justifyContent="center" alignItems="center" className='chatContainer'>
            <MainContainer style={{border:'none'}}>
              <ChatContainer >       
                <MessageList 
                style={{background:'black'}}
                  scrollBehavior="smooth" 
                  typingIndicator={isTyping ? <TypingIndicator content="AiBot is typing" style={{background:'black'}} /> : null }
                >
                  {messages.map((message, i) => {
                    return <Message key={i} model={message} style={{textAlign:'left'}} />
                  })}
                </MessageList>
                <MessageInput placeholder="Type message here" onSend={handleSend} style={{background:'black'}} />        
              </ChatContainer>
            </MainContainer>
          </Grid>
          <Grid item sm={4} xs={12} pt={{sm:15}}  pr={{md:5}}>
                  <AudioScreen messages={messages} />
          </Grid>
        </Grid>
      )
}

export default ChatScreen