import React from 'react'
import VoiceSelector from './VoiceSelector';
import { useState } from 'react';
import { TextField,Button, Stack } from '@mui/material';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';
const synth = window.speechSynthesis;

function AudioScreen(props) {


    const lastMessage = props.messages[props.messages.length-1];

    //console.log('last me', lastMessage.message);    
;
    const [selectedVoice, setSelectedVoice] = useState(0);

  if (!synth)
    return <span>Aw... your browser does not support Speech Synthesis</span>;

    const speak = (e) => {
      e.preventDefault();
      const synth = window.speechSynthesis;
      const utterance = new SpeechSynthesisUtterance(lastMessage.message);
      utterance.voice = synth.getVoices()[selectedVoice];
  
      synth.speak(utterance);
    };
  
  return (
    <form onSubmit={speak}>
    <Stack direction="column" spacing={2}>
      <VoiceSelector selected={selectedVoice} setSelected={setSelectedVoice} synth={synth} />
      <Button
      startIcon={<PlayCircleIcon />}
      type='submit'
      variant="contained"
      color='success'
      >Play Last Message</Button>
      </Stack>
    </form>

  );
}

export default AudioScreen