import { InputLabel, MenuItem, Select } from '@mui/material';
import { inputLabelClasses } from "@mui/material/InputLabel";

import React, { useState } from 'react'
import { useEffect } from 'react';
import { useCallback } from 'react';

function VoiceSelector(VoiceSelectorP) {

        const [voices, setVoices] = useState([]);
      
        const populateVoiceList = useCallback(() => {
          const newVoices = VoiceSelectorP.synth.getVoices();
          setVoices(newVoices);
        }, []);

        useEffect(() => {
          populateVoiceList();
          if (VoiceSelectorP.synth.onvoiceschanged !== undefined) {
            VoiceSelectorP.synth.onvoiceschanged = populateVoiceList;
          }
        }, [populateVoiceList]);
      
        return (
          <>
          
          <Select
          labelStyle={{ color: '#fff' }}
          sx={{
            color: "white",
            '.MuiOutlinedInput-notchedOutline': {
              borderColor: 'rgba(228, 219, 233, 0.25)',
            },
            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
              borderColor: 'rgba(228, 219, 233, 0.25)',
            },
            '&:hover .MuiOutlinedInput-notchedOutline': {
              borderColor: 'rgba(228, 219, 233, 0.25)',
            },
            '.MuiSvgIcon-root ': {
              fill: "white !important",
            }
          }}
            labelId="demo-simple-select-filled-label"
            id="demo-simple-select"
            value={VoiceSelectorP.selected}
            label="Select Voice"
            onChange={(e) => VoiceSelectorP.setSelected(parseInt(e.target.value))}
                    InputLabelProps={{
          sx: {
            color: "orange",
            [`&.${inputLabelClasses.shrink}`]: {
              color: "red"
            }
          }
        }}
          >
            {voices.map((voice, index) => (
              <MenuItem key={index} value={index}>{voice.name} ({voice.lang}) {voice.default && ' [Default]'}</MenuItem>
            ))}          
          </Select>

          </>
            
        );

      
}

export default VoiceSelector